	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/08/t.jpg\" alt=\"t.jpg\" border=\"0\" width=\"225\" height=\"300\" /> <img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/08/a.jpg\" alt=\"a.jpg\" border=\"0\" width=\"225\" height=\"325\" /></p>\n<p>I'm sorry, but when I saw these two posters yesterday, I knew they just were begging to be combined in a single post. The poster on the right has managed to hit a taboo-trifecta (race, nudity, homosexualtiy) with maybe even more taboos I haven't noticed (bad nail polish?).</p>\n<p>Two great tastes that go great... no, I won't finish that thought.</p>\n<p>(Left: Berlin CDU Bundestag candidate Vera Lengsfeld, \"We have more to offer\", seen at <a href=\"http://50percentdna.blogspot.com/2009/08/merkel.html\">50% of my DNA</a>. Right: Local Green poster in Kaarst, NRW, \"The only reason to vote black\", seen at <a href=\"http://www.amiexpat.com/2009/08/11/racist-tasteless-or-provocative/\">AmiExpat</a>)</p>","fields":{"title":"T and A Politics","date":"12 Aug 2009","url":"https://papascott.de/2009-08-12-t-and-a-politics/"}}},"pathContext":{"slug":"/2009-08-12-t-and-a-politics/","prev":{"fields":{"title":"Na?","date":"10 Aug 2009","slug":"/2009-08-10-na/"}},"next":{"fields":{"title":"My Health Insurance and Welcome to It","date":"14 Aug 2009","slug":"/2009-08-14-my-health-insurance-and-welcome-to-it/"}}}}

